import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from "@angular/material/dialog";
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl,
  FormArray
} from "@angular/forms";
import {Router } from '@angular/router';
import * as moment from 'moment';
import { ToastrManager } from 'ng6-toastr-notifications';
import { PrizeService } from 'src/app/idealz/services/prize.service';
import { ProductService } from 'src/app/idealz/services/product.service';
import { CampaignService } from 'src/app/idealz/services/campaign.service';

@Component({
  selector: 'app-add-edit-Compaign',
  templateUrl: './add-edit-Compaign.component.html',
  styleUrls: ['./add-edit-Compaign.component.scss']
})
export class AddEditCompaignComponent implements OnInit {
productForm: FormGroup;

  productList: any = [];
  prizeList: any = [];
  onStatus: boolean = false;
  productImage: any;
  prizeImage: any;
  submitted: boolean = false;
  history = window.history;
  startMinDate = moment(new Date()).subtract(1, 'd').format('YYYY-MM-DD')

  constructor( private formBuilder: FormBuilder, 
    private router:Router,
    private toastr: ToastrManager,
    private prizeService:PrizeService,
    private productService:ProductService,
    private campaignService:CampaignService) { 
      this.prizeService.prizeListing().subscribe(res=>{
        this.prizeList=res.data;
      });
      this.productService.productListing().subscribe(res=>{
        this.productList=res.data;
      })
    }

  ngOnInit() {
    this.productForm = this.formBuilder.group({
      name: new FormControl("",[Validators.required, Validators.maxLength(20), Validators.pattern(".*\\S.*[a-zA-z0-9 ]")]),
      arabicName: new FormControl(""),
      productId: new FormControl("", [Validators.required]),
      quantity: new FormControl("", [Validators.required]),
      image: new FormControl("", [Validators.required]),
      earlyBird: new FormControl(),
      showOnTop: new FormControl(),
      itemNumber: new FormControl("", [Validators.required]),
      ticketPerProduct: new FormControl("", [Validators.required]),
      earlyTicketCount: new FormControl("", [Validators.required]),
      perProductDonate:new FormControl("", [Validators.required]),
      maxUserProduct: new FormControl(""),
      prizeId:new FormControl("", [Validators.required]),   
      startDate: new FormControl("", Validators.compose([Validators.required]))
    });
  }

  async profilePic(event) {
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        this.productImage = event.target.result;
        this.productForm.controls['image'].setValue(this.productImage);
      };
    }
  } 

  onSubmit(){
    this.submitted = true;
    if(this.submitted && this.productForm.valid){ 
      var data = this.productForm.value; 
      data['status'] =false;
      this.campaignService.addCampaign(data).subscribe(res=>{
        this.toastr.successToastr("Campaign added", '', {
          maxShown: 1
        }); 
      })
      this.router.navigate(['/campaign/campaign'])
    }
  }
  onChange(event){
    this.onStatus = event;
  }

  afterPickerOpen() {
    document.getElementsByTagName('html')[0].style.top = '';
  }
}
 
